<template>
  <!-- Page wrapper start -->
  <div class="page-wrapper">
    <!-- Sidebar wrapper start -->
    <Sidebar />
    <!-- Sidebar wrapper end -->

    <!-- Main container start -->
    <div class="main-container">
      <!-- Page header starts -->
      <Navbar page="Tesouraria / Fluxo de Caixa" />
      <!-- Page header ends -->

      <!-- Content wrapper scroll start -->
      <div class="content-wrapper-scroll">
        <!-- Content wrapper start -->
        <div class="content-wrapper">
          <div class="row">
            <div class="col-12">
              <div class="card p-2">
                <div class="row p-2">
                  <div class="col-md-6">
                    <label class="mb-1">Selecione o per√≠odo</label><br />
                    <div class="d-flex align-items-center">
                      <b class="me-1">DE</b>
                      <input
                        type="date"
                        class="form-control"
                        v-model="filter_data.start_date"
                      />
                      <b class="mx-1">A</b>
                      <input
                        type="date"
                        class="form-control"
                        v-model="filter_data.end_date"
                      />
                    </div>
                  </div>

                  <div class="col-md-6">
                    <label class="text-white mb-1">.</label><br />
                    <button
                      @click="filterFluxo"
                      class="btn btn-info text-uppercase fw-bold"
                    >
                      <span class="fas fa-search"></span> Filtrar
                    </button>
                    <button
                      @click="generatePDF"
                      v-if="entradas.length > 0 || despesas.length > 0"
                      class="btn btn-danger mx-2 text-uppercase fw-bold"
                    >
                      <span class="fas fa-file-pdf"></span> Imprimir relat√≥rio
                    </button>
                  </div>
                </div>
              </div>

              <!-- ENTRADAS -->
              <div class="card">
                <div class="card-header">
                  <h5>ENTRADAS</h5>
                </div>
                <div class="card-body">
                  <DataTable
                    :data="entradas"
                    :columns="entradasColums"
                    :options="{ select: true, language: language }"
                    class="display table table-striped table-bordered"
                  >
                    <thead>
                      <tr class="bg-secondary">
                        <th>DESCRI√á√ÉO</th>
                        <th>ESTUDANTE</th>
                        <th>VALOR</th>
                        <th>FUNCION√ÅRIO</th>
                        <th>DATA</th>
                      </tr>
                    </thead>
                  </DataTable>
                </div>
                <div class="card-footer">
                  <div class="float-end">
                    <p>
                      Resultado:
                      <b>{{ numberFormat(totalReceita, 2, ",", ".") }} Kz</b>
                    </p>
                  </div>
                </div>
              </div>

              <!-- SA√çDAS -->
              <div class="card">
                <div class="card-header">
                  <h5>SA√çDAS</h5>
                </div>
                <div class="card-body">
                  <DataTable
                    :data="despesas"
                    :columns="saidasColums"
                    :options="{ select: true, language: language }"
                    class="display table table-striped table-bordered"
                  >
                    <thead>
                      <tr class="bg-secondary">
                        <th>DESCRI√á√ÉO</th>
                        <th>REFER√äNCIA</th>
                        <th>VALOR</th>
                        <th>FUNCION√ÅRIO</th>
                        <th>DATA</th>
                      </tr>
                    </thead>
                  </DataTable>
                </div>
                <div class="card-footer">
                  <div class="float-end">
                    <p>
                      Resultado:
                      <b>{{ numberFormat(totalDespesa, 2, ",", ".") }} Kz</b>
                    </p>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <!-- App Footer start -->
        <Footer />
        <!-- App footer end -->

        <Loader v-if="isLoading" />
      </div>
      <!-- Content wrapper scroll end -->
    </div>
  </div>
  <!-- Page wrapper end -->
</template>

<script>
import "../static";
import Sidebar from "../components/Sidebar.vue";
import Navbar from "../components/Navbar.vue";
import Footer from "../components/Footer.vue";
import Loader from "../components/Loader.vue";
import { api, numberFormat, language, url } from "../../../helpers/api";
import moment from "moment";
import DataTable from "datatables.net-vue3";
import DataTablesCore from "datatables.net";
import Select from "datatables.net-select";

DataTable.use(DataTablesCore);
DataTable.use(Select);

export default {
  name: "fluxo_de_caixa_view",
  components: { Sidebar, Navbar, Footer, Loader, DataTable },
  data() {
    return {
      despesas: [],
      entradas: [],
      totalDespesa: 0,
      totalReceita: 0,
      isLoading: false,
      filter_data: {
        start_date: "",
        end_date: "",
      },
      entradasColums: [
        { data: "descricao" },
        {
          data: null,
          render: (data) => `${data.Estudante?.nome || ""}`,
        },
        {
          data: "totalPrice",
          render: (data) => `${numberFormat(data, 2, ",", ".")}`,
        },
        {
          data: null,
          render: (data) => `${data.User?.nome || ""}`,
        },
        {
          data: "createdAt",
          render: (data) => moment(data).format("DD-MM-YYYY HH:mm:ss"),
        },
      ],
      saidasColums: [
        { data: "descricao" },
        { data: "referencia" },
        {
          data: "valor",
          render: (data) => `${numberFormat(data, 2, ",", ".")}`,
        },
        {
          data: null,
          render: (data) => `${data.User?.nome || ""}`,
        },
        {
          data: "createdAt",
          render: (data) => moment(data).format("DD-MM-YYYY HH:mm:ss"),
        },
      ],
      language: language,
    };
  },
  methods: {
    filterFluxo() {
      this.totalDespesa = 0;
      this.totalReceita = 0;
      const { start_date, end_date } = this.filter_data;

      if (!start_date || !end_date) {
        this.$swal.fire({
          title: "Notifica√ß√£o",
          icon: "warning",
          text: "Preencha todos os campos",
        });
      } else {
        api
          .get(`/fluxo-de-caixa/start/${start_date}/end/${end_date}`)
          .then((res) => {
            this.despesas = res.data.saidas;
            this.entradas = res.data.entradas;
            res.data.saidas.forEach((item) => {
              this.totalDespesa += item.valor;
            });
            res.data.entradas.forEach((item) => {
              this.totalReceita += item.totalPrice;
            });
          })
          .catch((err) => console.error(err));
      }
    },

    generatePDF() {
      if (this.entradas.length === 0 && this.despesas.length === 0) {
        this.$swal.fire({
          title: "Aten√ß√£o",
          icon: "info",
          text: "N√£o h√° dados para imprimir.",
        });
        return;
      }

      const printWindow = window.open("", "_blank");
      const logoUrl = `${url}/api/mediafiles/castro.jpeg`;
      const totalEntradas = numberFormat(this.totalReceita, 2, ",", ".");
      const totalSaidas = numberFormat(this.totalDespesa, 2, ",", ".");
      const saldo = numberFormat(this.totalReceita - this.totalDespesa, 2, ",", ".");

      const rowsEntradas = this.entradas
        .map(
          (e) => `
        <tr>
          <td>${e.descricao || ""}</td>
          <td>${e.Estudante?.nome || ""}</td>
          <td>${numberFormat(e.totalPrice, 2, ",", ".")} Kz</td>
          <td>${e.User?.nome || ""}</td>
          <td>${moment(e.createdAt).format("DD-MM-YYYY HH:mm:ss")}</td>
        </tr>`
        )
        .join("");

      const rowsSaidas = this.despesas
        .map(
          (e) => `
        <tr>
          <td>${e.descricao || ""}</td>
          <td>${e.referencia || ""}</td>
          <td>${numberFormat(e.valor, 2, ",", ".")} Kz</td>
          <td>${e.User?.nome || ""}</td>
          <td>${moment(e.createdAt).format("DD-MM-YYYY HH:mm:ss")}</td>
        </tr>`
        )
        .join("");

      const htmlContent = `
        <html>
        <head>
          <title>Relat√≥rio de Fluxo de Caixa</title>
          <style>
            body {
              font-family: Arial, sans-serif;
              margin: 20px;
              color: #222;
            }
            header {
              text-align: center;
              border-bottom: 2px solid #333;
              margin-bottom: 10px;
              padding-bottom: 10px;
            }
            header img {
              width: 80px;
              height: 80px;
              border-radius: 8px;
            }
            h1 {
              margin-top: 10px;
              font-size: 20px;
            }
            table {
              width: 100%;
              border-collapse: collapse;
              margin-bottom: 20px;
            }
            th, td {
              border: 1px solid #999;
              padding: 6px 8px;
              text-align: left;
              font-size: 13px;
            }
            th {
              background-color: #f2f2f2;
            }
            .totais {
              margin-top: 10px;
              text-align: right;
              font-size: 14px;
            }
            footer {
              text-align: center;
              font-size: 12px;
              margin-top: 40px;
              color: #555;
            }
            @media print {
              button { display: none; }
              body { margin: 0; }
            }
          </style>
        </head>
        <body>
          <header>
            <img src="${logoUrl}" alt="Logo"/>
            <h2>INSTITUTO POLIT√âCNICO PRIVADO CASTRO</h2>
            <p>"A Luz do Saber"</p>
            <h1>RELAT√ìRIO DE FLUXO DE CAIXA</h1>
            <p>Per√≠odo: ${moment(this.filter_data.start_date).format(
              "DD/MM/YYYY"
            )} a ${moment(this.filter_data.end_date).format("DD/MM/YYYY")}</p>
          </header>

          <h3>Entradas</h3>
          <table>
            <thead>
              <tr>
                <th>Descri√ß√£o</th>
                <th>Estudante</th>
                <th>Valor</th>
                <th>Funcion√°rio</th>
                <th>Data</th>
              </tr>
            </thead>
            <tbody>${rowsEntradas}</tbody>
          </table>

          <h3>Sa√≠das</h3>
          <table>
            <thead>
              <tr>
                <th>Descri√ß√£o</th>
                <th>Refer√™ncia</th>
                <th>Valor</th>
                <th>Funcion√°rio</th>
                <th>Data</th>
              </tr>
            </thead>
            <tbody>${rowsSaidas}</tbody>
          </table>

          <div class="totais">
            <p><strong>Total de Entradas:</strong> ${totalEntradas} Kz</p>
            <p><strong>Total de Sa√≠das:</strong> ${totalSaidas} Kz</p>
            <p><strong>Saldo a Transitar:</strong> ${saldo} Kz</p>
          </div>

          <footer>
            <p>Gerado em ${moment().format("DD/MM/YYYY HH:mm")}</p>
            <p>______________________________</p>
            <p>Assinatura do Respons√°vel</p>
          </footer>

          <button onclick="window.print()">üñ®Ô∏è Imprimir</button>
        </body>
        </html>
      `;

      printWindow.document.write(htmlContent);
      printWindow.document.close();
    },

    numberFormat,
  },
};
</script>

<style>
.selected {
  background-color: blue;
}
</style>